/* CSS RESET */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Titillium Web", sans-serif;
  font-weight: 400;
  font-style: normal;
}

html, body {
  width: 100%;
  overflow-x: hidden;
}

/* ================= FOOTER ================= */
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  width: 100%;
}

/* DARK MODE SUPPORT */
#dark {
  background-color: rgb(70, 70, 70); /* solid thick medium grey */
  color: #f4eee0;
}

/* ================= MEDIA QUERIES ================= */
@media (max-width: 768px) {
  .footer {
    font-size: 24px;
    padding: 16px;
  }
}

@media (max-width: 480px) {
  .footer {
    font-size: 20px;
    padding: 14px;
  }
}
