/* ========== PROJECTS SECTION ========== */
.project {
  margin: 30px 10px 30px 210px;
  border-radius: 12px;
  background: linear-gradient(145deg, #002855, #00509e);
  box-shadow: 0 4px 20px rgba(30, 144, 255, 0.5), 0 0 15px rgba(30, 144, 255, 0.2);
  padding: 40px;
  padding-top:80px;
}

.project h2 {
  font-family: cursive;
  text-align: center;
  margin-bottom: 20px;
  font-size: 2.2rem;
  background: linear-gradient(135deg, #7dd3fc, #ffffff, #7dd3fc);
  background-size: 200% auto;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 4s ease-in-out infinite;
}

.project p {
  margin-top: 30px;
  color: #ffffff;
  text-align: center;
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom:40px;
}
.prjt{
  text-align:center;
  margin-top:20px;
  font-family: cursive;
  font-size:25px;
}
/* ========== GRID LAYOUT ========== */
.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 30px;
}

.card {
  cursor: pointer;
  border-radius: 12px;
  background: linear-gradient(145deg, #1e90ff, #005bb5);
  color: #ffffff;
  padding: 15px;
  transition: transform 0.5s ease, box-shadow 0.5s ease;
  box-shadow: 0 2px 8px rgba(30, 144, 255, 0.3);
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card:hover {
  background-color: #1e90ff;
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 0 20px 4px rgba(255, 255, 255, 0.9);
}

.card-notify-badge-rt{
  position:relative;
  left: -10px;
  top: -20px;
  background: #fe424d;
  text-align: center;
  border-radius: 30px;
  color: white;
 padding: 10px 12px;
  font-size: 18px;
}

.card-notify-badge-fs{
  position: relative;
  left: -10px;
  top: -20px;
  background: #fe424d;
  text-align: center;
  border-radius: 30px;
  color: white;
  padding: 10px 12px;
  font-size: 18px;
}

.card-detail-badge {
  background-color: #fe424d;
  text-align: center;
  border-radius: 30px;
  color: white;
  padding: 5px 10px;
  font-size: 14px;
  margin: 3px;
}

.card-image-overly {
  font-size: 20px;
  text-align: center;
}

.card-image-overly span {
  display: inline-block;
  margin-left: 5px;
}

.project .card img {
  height: 300px;
  width:100%;
  border-radius: 8px;
}

.ad-title h5,
.ad-title h6 {
  color: #ffffff;
  margin: 10px 0;
}

.ad-btn-git{
  text-transform: uppercase;
  width: 150px;
  height: 40px;
  display: block;
  text-align: center;
  background-color: black;
  color: white;
  border-radius: 80px;
  font-size: 14px;
  line-height: 38px;
  text-decoration: none;
  margin: 12px auto;
  position: relative;
  overflow: hidden;

}

.ad-btn-git:hover {
  background-color: white;
  color:black;
  border: 1px solid black;
  transition: all 0.5s ease;
  box-shadow: 12px 15px 20px rgba(46, 61, 73, 0.15);
}

.ad-btn-live{
  text-transform: uppercase;
  width: 150px;
  height: 40px;
  display: block;
  text-align: center;
  background-color:  #fe424d;
  color: white;
  border-radius: 80px;
 font-size: 14px;
  line-height: 38px;
  text-decoration: none;
  margin: 12px auto;
  position: relative;
  overflow: hidden;

}

.ad-btn-live:hover {
  background-color: white;
  color: #fe424d;
  border: 1px solid  #fe424d;
  transition: all 0.5s ease;
  box-shadow: 12px 15px 20px rgba(46, 61, 73, 0.15);
}

@keyframes shine {
  0% {
    background-position: 200% center;
  }
  100% {
    background-position: -200% center;
  }
}

/* ========== DARK MODE ========== */
#dark .project {
  background: linear-gradient(145deg, #1b1b1b, #2c2c2c);
}

#dark .card {
  background: linear-gradient(145deg, #1a2b58, #3c55c2);
  box-shadow: 0 0 15px 2px rgba(60, 90, 220, 0.7);
}

#dark .card:hover {
  background: linear-gradient(145deg, #1a2b58, #3c55c2);
  box-shadow: 0 0 30px 8px rgba(255, 255, 255, 0.9);
}

#dark .project h2 {
  background: linear-gradient(135deg, #a5e0ff, #f2ccff, #a5e0ff);
  background-size: 200% auto;
  animation: shine 4s ease-in-out infinite;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#dark .project p,
#dark .ad-title h5,
#dark .ad-title h6 {
  color: #d1e9ff;
}

/* ========== RESPONSIVE DESIGN ========== */
@media (max-width: 600px) {
  .project {
    margin: 20px 10px;
    padding: 25px;
    margin-top: 30px;
  }

  .project h2 {
    font-size: 1.6rem;
  }

  .card-image-overly {
    font-size: 16px;
  }

  .ad-btn {
    width: 130px;
    height: 36px;
    font-size: 14px;
    line-height: 34px;
  }
}
