/* ========== EDUCATION SECTION ========== */
.education {
  padding: 100px 0px;
  margin: 50px 0px;
  margin-left: 210px;
  margin-right: 10px;
  border-radius: 12px;
  background: linear-gradient(145deg, #002855, #00509e);
  box-shadow: 0 4px 20px rgba(30, 144, 255, 0.5), 0 0 15px rgba(30, 144, 255, 0.2);
}

/* ========== MAIN HEADING ========== */
.education h2 {
 
  font-size: 2.2rem;
  text-align: center;
  background: linear-gradient(135deg, #7dd3fc, #ffffff, #7dd3fc);
  background-size: 200% auto;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 4s ease-in-out infinite;
 
  letter-spacing: 1px;
  font-family:cursive;
  margin-bottom: 30px;
}

/* ========== TIMELINE TEXT STYLES (Light Mode) ========== */
.education .vertical-timeline-element-title,
.education .vertical-timeline-element-subtitle,
.education .vertical-timeline-element-date {
  font-family: 'Segoe UI', sans-serif;
  font-weight: 600;
  color: #1a4b91; /* deep bluish shade */
  background: none;
  -webkit-background-clip: initial;
  -webkit-text-fill-color: initial;
  animation: none;
  transition: color 0.3s ease, text-shadow 0.3s ease;
  text-shadow: 0 0 3px rgba(30, 144, 255, 0.2);
}

.education .vertical-timeline-element-title {
  font-size: 1.4rem;
  margin-bottom: 4px;
}

.education .vertical-timeline-element-subtitle {
  font-size: 1.1rem;
  margin-bottom: 4px;
  color: #2671d2; /* slightly brighter blue for contrast */
}

.education .vertical-timeline-element-date {
  font-size: 1rem;
  font-style: italic;
  color: #1e90ff; /* DodgerBlue */
  text-shadow: 0 0 4px rgba(30, 144, 255, 0.4);
}

/* ========== SHINE TEXT ANIMATION ========== */
@keyframes shine {
  0% {
    background-position: 200% center;
  }
  100% {
    background-position: -200% center;
  }
}

/* ========== DARK MODE STYLING ========== */
#dark .education {
  background: linear-gradient(145deg, #0a0f1e, #142032);
  box-shadow: 0 0 30px 8px rgba(0, 136, 255, 0.3);
}

#dark .education h2 {
  background: linear-gradient(135deg, #a5e0ff, #f2ccff, #a5e0ff);
  background-size: 200% auto;
  animation: shine 4s ease-in-out infinite;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#dark .education .vertical-timeline-element-title,
#dark .education .vertical-timeline-element-subtitle,
#dark .education .vertical-timeline-element-date {
  color:linear-gradient(145deg, #000428, #004e92);
  background: none;
  -webkit-background-clip: initial;
  -webkit-text-fill-color: initial;
  animation: none;
}

/* ========== RESPONSIVE DESIGN ========== */
@media (max-width: 600px) {
  .education {
    margin: 20px 10px;
    padding: 25px;
    margin-top: 30px;
  }

  .education h2 {
    padding-top: 20px;
    font-size: 1.6rem;
  }
}
