/* ========== WORK EXPERIENCE SECTION (LIGHT MODE) ========== */
.work {
  background: linear-gradient(145deg, #002855, #00509e);
  margin-left: 210px;
  margin-right: 10px;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(30, 144, 255, 0.5), 0 0 15px rgba(30, 144, 255, 0.2);
  border-bottom: none !important;
  padding:40px;
  padding-top:80px;

}

/* .work-exp {
  padding: 80px 0px;
} */

.work-exp h2 {

  font-size: 2.2rem;
  text-align: center;
  background: linear-gradient(135deg, #7dd3fc, #ffffff, #7dd3fc);
  background-size: 200% auto;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 4s ease-in-out infinite;
  letter-spacing: 1px;
  font-family: cursive;
  margin-bottom: 30px;
  margin-top:20px;
}

.work-exp .vertical-timeline-element-title,
.work-exp .vertical-timeline-element-subtitle,
.work-exp .vertical-timeline-element-date {
  font-family: 'Segoe UI', sans-serif;
  font-weight: 600;
  color: #1a4b91;
  text-shadow: 0 0 3px rgba(30, 144, 255, 0.2);
  transition: color 0.3s ease, text-shadow 0.3s ease;
  border-bottom: none !important;
}

.work-exp .vertical-timeline-element-title {
  font-size: 1.4rem;
  margin-bottom: 4px;
}

.work-exp .vertical-timeline-element-subtitle {
  font-size: 1.1rem;
  margin-bottom: 4px;
  color: #2671d2;
}

.work-exp .vertical-timeline-element-date {
  font-size: 1rem;
  font-style: italic;
  color: #1e90ff;
  text-shadow: 0 0 4px rgba(30, 144, 255, 0.4);
  border-bottom: none !important;
}

/* ========== DARK MODE STYLING ========== */
#dark .work {
  background: linear-gradient(145deg, #0a0f1e, #142032);
  box-shadow: 0 0 30px 8px rgba(0, 136, 255, 0.3);
  border-bottom: none !important;
}

#dark .work-exp h2 {
  background: linear-gradient(135deg, #a5e0ff, #f2ccff, #a5e0ff);
  background-size: 200% auto;
  animation: shine 4s ease-in-out infinite;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#dark .work-exp .vertical-timeline-element-title,
#dark .work-exp .vertical-timeline-element-subtitle,
#dark .work-exp .vertical-timeline-element-date {
  color:linear-gradient(145deg, #000428, #004e92);
  text-shadow: none;
  border-bottom: none !important;
}

/* ========== SHINE TEXT ANIMATION ========== */
@keyframes shine {
  0% {
    background-position: 200% center;
  }
  100% {
    background-position: -200% center;
  }
}

/* ========== RESPONSIVE DESIGN ========== */
@media (max-width: 600px) {
  .work {
    margin: 20px 10px;
    padding: 25px;
    margin-top: 30px;
  }

  .work-exp h2 {
    font-size: 1.6rem;
   
  }
}
