.about {
  display: flex;
  flex-wrap: wrap;
  margin: 30px 10px 30px 210px; /* fixed left margin on all except small screens */
  padding-top: 80px;
  border-radius: 12px;
  align-items: center;
  background: linear-gradient(145deg, #000428, #004e92);
  box-shadow: 0 4px 20px rgba(0, 136, 204, 0.5), 0 0 15px rgba(0, 255, 255, 0.1);
}

.about-img {
  flex: 1 1 40%;
  display: flex;
  justify-content: center;
  padding: 10px;
}

.about-img img {
  border-radius: 50%;
  width: 250px;
  height: 250px;
  object-fit: cover;
  border: 4px solid #004e92;
  box-shadow: 0 0 20px rgba(0, 102, 204, 0.7);
}

.about-content {
  flex: 1 1 55%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  padding: 20px;
}

.about-content h1 {
  font-family: cursive;
  text-align: center;
  margin-bottom: 10px;
  font-size: 2.2rem;
  background: linear-gradient(135deg, #4dabf7, #ffffff, #4dabf7);
  background-size: 200% auto;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 4s ease-in-out infinite;
}

.about-content p {
  text-align: justify;
  padding: 10px;
  font-size: 1.05rem;
  line-height: 1.6;
}

/* ======== DARK MODE ========= */
#dark .about {
  background: linear-gradient(145deg, #0d1117, #1a1f2b);
  box-shadow: 0 4px 20px rgba(0, 136, 204, 0.5), 0 0 15px rgba(0, 255, 255, 0.1);
}

#dark .about-content h1 {
  background: linear-gradient(135deg, #b0d0ff, #d0a0ff, #b0d0ff);
  background-size: 200% auto;
  animation: shine 4s ease-in-out infinite;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* ========== Responsive Styles ========== */

/* Tablets and bigger devices keep left margin */
@media (max-width: 1024px) and (min-width: 601px) {
  .about {
    flex-direction: column;
    margin: 20px 10px 20px 210px; /* keep left margin fixed */
    padding: 30px;
  }

  .about-img {
    margin-bottom: 20px;
  }

  .about-img img {
    width: 200px;
    height: 200px;
  }

  .about-content {
    align-items: center;
    text-align: center;
    padding: 10px;
  }

  .about-content h1 {
    font-size: 1.8rem;
  }

  .about-content p {
    font-size: 1rem;
    line-height: 1.5;
  }
}

/* Small Screens (mobile and below 600px) - reduce left margin */
@media (max-width: 600px) {
  .about {
    flex-direction: column;
    margin: 15px 10px 15px 10px; /* reduce left margin */
    padding: 20px;
  }

  .about-img img {
    width: 150px;
    height: 150px;
  }

  .about-content h1 {
    font-size: 1.5rem;
  }

  .about-content p {
    font-size: 0.95rem;
  }
}

/* ========== Animation Keyframes ========== */
@keyframes shine {
  0% {
    background-position: 0% center;
  }
  50% {
    background-position: 100% center;
  }
  100% {
    background-position: 0% center;
  }
}
