.contact {
  margin: 30px 10px 30px 210px; /* fixed left margin on large screens */
  padding: 40px;
  padding-top: 80px;
  border-radius: 12px;
  background: linear-gradient(145deg, #000428, #004e92);
  box-shadow: 0 4px 20px rgba(0, 136, 204, 0.5), 0 0 15px rgba(0, 255, 255, 0.1);
  color: #ffffff;
  font-family: "Lexend Deca", sans-serif;
  box-sizing: border-box;
}

.contact h2,
.contact h3 {
  color: #4dabf7;
  text-align: center;
  text-shadow: 0 0 8px rgba(0, 191, 255, 0.8);
}

.contact h2 {
  font-size: 2.2rem;
  margin-bottom: 20px;
}

.contact h3 {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.contactme {
  font-family: cursive;
  text-align: center;
  margin: 26px 0;
  font-size: 2.2rem;
  background: linear-gradient(135deg, #4dabf7, #ffffff, #4dabf7);
  background-size: 200% auto;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 4s ease-in-out infinite;
  margin-top: 90px;
}

.contact-row {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: space-between;
}

.contact-left,
.contact-right {
  flex: 1;
  min-width: 300px;
  max-width: 48%;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contact-form input,
.contact-form textarea {
  padding: 12px 18px;
  border-radius: 10px;
  border: none;
  font-size: 1rem;
  font-family: inherit;
  background: white;
  color: black;
  box-shadow: 0 2px 8px rgba(0, 68, 136, 0.2);
  transition: background 0.3s ease, box-shadow 0.3s ease;
}

.contact-form input:focus,
.contact-form textarea:focus {
  outline: none;
  background: white;
  box-shadow: 0 0 10px rgba(0, 136, 204, 0.5);
}

.contact-form textarea {
  resize: vertical;
  min-height: 100px;
  max-height: 200px;
}

.contact-form button {
  align-self: center;
  padding: 12px 32px;
  font-size: 1rem;
  border: none;
  border-radius: 10px;
  background-color: #fe424d;
  color: white;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.contact-form button:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 18px rgba(0, 102, 204, 0.6);
}

/* 🌐 Social Media Section */
.socialmedia {
  margin-left: 35px;
  margin-bottom: 25px;
  word-break: break-word;
  max-width: 100%;
}

.social-link {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  text-decoration: none;
  color: white;
  font-size: 20px;
  transition: transform 0.3s ease;
}

.social-link:hover {
  transform: scale(1.05);
  color: #4dabf7;
}

.social-link .icon {
  font-size: 28px;
}

/* 🌊 Boat-like tilt animation */
.boat-tilt-animation:hover {
  animation: tiltRock 1.2s ease-in-out 1;
}

@keyframes tiltRock {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-3deg);
  }
  50% {
    transform: rotate(3deg);
  }
  75% {
    transform: rotate(-2deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

/* ✨ Text gradient animation */
@keyframes shine {
  0% {
    background-position: 0% center;
  }
  50% {
    background-position: 100% center;
  }
  100% {
    background-position: 0% center;
  }
}

/* 🌙 Dark Mode */
#dark .contact {
  background: linear-gradient(145deg, #0d1117, #1a1f2b);
}

#dark .contact h2,
#dark .contact h3,
#dark .contactme {
  background: linear-gradient(135deg, #b0d0ff, #d0a0ff, #b0d0ff);
  background-size: 200% auto;
  animation: shine 4s ease-in-out infinite;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* ====== RESPONSIVE STYLES ====== */

/* Large screens - keep left margin */
@media (min-width: 1025px) {
  .contact {
    margin: 30px 10px 30px 210px;
    padding: 40px;
    padding-top: 80px;
  }

  .contact-left,
  .contact-right {
    max-width: 48%;
  }

  .contact-row {
    flex-wrap: nowrap;
  }
}

/* Medium screens/tablets */
@media (max-width: 1024px) and (min-width: 769px) {
  .contact {
    margin: 30px 15px 30px 210px; /* keep left margin for tablets */
    padding: 35px 25px;
    padding-top: 70px;
  }

  .contact-left,
  .contact-right {
    max-width: 48%;
  }
}

/* Smaller tablets and large phones */
@media (max-width: 768px) and (min-width: 481px) {
  .contact {
    margin: 25px 15px 25px 210px; /* keep left margin */
    padding: 30px 20px;
    padding-top: 60px;
  }

  .contact-left,
  .contact-right {
    max-width: 100%;
  }

  .contact-row {
    flex-direction: column;
    gap: 30px;
  }
}

/* Phones and small devices */
@media (max-width: 480px) {
  .contact {
    margin: 20px 10px;
    padding: 20px 15px;
    padding-top: 50px;
  }

  .contact-left,
  .contact-right {
    max-width: 100%;
  }

  .contact-row {
    flex-direction: column;
    gap: 25px;
  }

  .contact h2,
  .contact h3,
  .contactme {
    font-size: 1.5rem;
  }

  .social-link {
    font-size: 18px;
  }

  .social-link .icon {
    font-size: 24px;
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 0.95rem;
    padding: 10px 14px;
  }

  .contact-form button {
    padding: 10px 28px;
    font-size: 0.95rem;
  }
}
