/* ---------- HOME CONTAINER ---------- */
.home-container {
  height: 300px;
  color: white;
  padding-left: 150px;
  margin-left: 150px;
  width: 100%;
}

/* ---------- BUTTONS & ICONS ROW ---------- */
.home-buttons {
  margin-top: 40px;          /* ⬆️ larger top gap */
  display: flex;
  align-items: center;
  gap: 30px;                 /* ⬆️ larger spacing between items */
  flex-wrap: wrap;
}

/* ---------- ICON LINKS ---------- */

.icon-link-linkedin{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: white;
  background-color:white;
  text-decoration: none;
  font-size: 40px;           /* safeguard: if size prop missing */
  transition: transform 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
  border-radius:10%;
}

.icon-link-linkedin:hover {
  color: #4dabf7;
  transform: scale(1.15);
  box-shadow: rgba(255, 255, 255, 0.7) 0px 0px 30px,
            rgba(255, 255, 255, 0.5) 0px 0px 60px,
            rgba(255, 255, 255, 0.4) 0px 0px 90px,
            rgba(255, 255, 255, 0.3) 0px 0px 120px;
  
}

.icon-link-github{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: white;
  background-color:black;
  text-decoration: none;
  font-size: 40px;           /* safeguard: if size prop missing */
  transition: transform 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
  border-radius:50%;
}

.icon-link-github:hover {
  color: #4dabf7;
  transform: scale(1.15);
  box-shadow: rgba(255, 255, 255, 0.7) 0px 0px 30px,
            rgba(255, 255, 255, 0.5) 0px 0px 60px,
            rgba(255, 255, 255, 0.4) 0px 0px 90px,
            rgba(255, 255, 255, 0.3) 0px 0px 120px;
  
}
 .intro{
    font-family: "Winky Rough", sans-serif !important;
  }
/* ---------- RESUME BUTTON ---------- */
.home-buttons .btn-cv {
  display: inline-block;
  background-color: #fe424d;
  color: white;
  padding: 14px 28px;        /* ⬆️ bigger button */
  font-size: 1.1rem;         /* ⬆️ bigger text */
  border: none;
  border-radius: 10px;
  text-decoration: none;
  cursor: pointer;
  transition: opacity 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}

.home-buttons .btn-cv:hover {
  opacity: 0.85;
  transform: scale(1.07);
  box-shadow: 0 0 14px rgba(255, 255, 255, 0.9);  /* 💡 white glow */
}

/* ---------- THEME TOGGLE BUTTON (unchanged) ---------- */
.theme-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  background-color:grey;
  margin-top: 20px;
  text-align: center;
  position: fixed;
  right: 0;
  cursor: pointer;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  z-index: 1;
}

/* ---------- MOBILE ADJUSTMENTS ---------- */
@media (max-width: 600px) {
  .home-container {
    padding: 0 20px;
    margin: 50px 0 0;
    width: 100%;
  }
  .intro,
  .typewriter {
    font-size: 24px;
    margin-left: 0;
    
  }
  .typewriter{
    font-family: "Winky Rough", sans-serif !important;
  }
  .home-buttons {
    justify-content: center;
    gap: 20px;
    margin-top: 30px;
  }
}

/* ---------- HEADINGS ---------- */
.intro {
  font-size: 30px;
  margin-top: 30px;
}
.typewriter {
  font-size: 25px;
  margin-top: 30px;
  color: white;

}
