/* ========== TECH SECTION CONTAINER ========== */
.tech {
  margin: 30px 10px 30px 210px; /* fixed left margin */
  border-radius: 12px;
  background: linear-gradient(145deg, #002855, #00509e);
  box-shadow: 0 4px 20px rgba(30, 144, 255, 0.5), 0 0 15px rgba(30, 144, 255, 0.2);
  padding: 40px;
  padding-top: 80px;
}

/* ========== GRID LAYOUT FOR CARDS ========== */
.techstack {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin-top: 40px;
}

/* ========== TECH CARD STYLING ========== */
.techstack .card {
  cursor: pointer;
  border-radius: 14px;
  background: linear-gradient(145deg, #1e90ff, #005bb5);
  color: #ffffff;
  padding: 15px;
  box-sizing: border-box;
  transition: transform 0.5s ease 0.1s, box-shadow 0.5s ease 0.1s;
  box-shadow: 0 2px 8px rgba(30, 144, 255, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 110px;
  font-size: 1.1rem;
}

.techstack .card:hover {
  background-color: #1e90ff;
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 0 20px 4px rgba(255, 255, 255, 0.9);
}

.techstack .card:hover .tech-icon {
  color: #ffffff;
}

.techstack .tech-icon {
  font-size: 2.4rem;
  color: #ffffff;
  margin-bottom: 10px;
}

/* ========== SECTION HEADING STYLING ========== */
.section-heading {
  text-align: center;
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 40px;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* ========== HEADER TEXTS ========== */
.tech h2 {
  font-family: cursive;
  text-align: center;
  margin-bottom: 10px;
  font-size: 2.2rem;
  background: linear-gradient(135deg, #7dd3fc, #ffffff, #7dd3fc);
  background-size: 200% auto;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 4s ease-in-out infinite;
}

.tech p {
  margin-top: 30px;
  color: #ffffff;
  text-align: center;
  font-size: 1rem;
  line-height: 1.5;
}

/* ========== SHINE TEXT ANIMATION ========== */
@keyframes shine {
  0% {
    background-position: 200% center;
  }
  100% {
    background-position: -200% center;
  }
}

/* ========== DARK MODE STYLING ========== */
#dark .tech {
  background: linear-gradient(145deg, #1b1b1b, #2c2c2c);
}

#dark .techstack .card {
  background: linear-gradient(145deg, #1a2b58, #3c55c2);
  box-shadow: 0 0 15px 2px rgba(60, 90, 220, 0.7);
  color: #ffffff;
}

#dark .techstack .card:hover {
  background: linear-gradient(145deg, #1a2b58, #3c55c2);
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 0 30px 8px rgba(255, 255, 255, 0.9);
}

#dark .techstack .card:hover .tech-icon {
  color: #ffffff;
}

#dark .tech h2 {
  background: linear-gradient(135deg, #a5e0ff, #f2ccff, #a5e0ff);
  background-size: 200% auto;
  animation: shine 4s ease-in-out infinite;
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#dark .section-heading {
  color: #d1e9ff;
}

/* ========== RESPONSIVE DESIGN (Mobile First) ========== */

/* Large screens and desktops (more than 1024px) */
@media (min-width: 1025px) {
  .tech {
    margin: 30px 10px 30px 210px;
    padding: 40px;
  }

  .techstack {
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
  }

  .tech h2 {
    font-size: 2.2rem;
  }
}

/* Medium Screens (Tablets) */
@media (max-width: 1024px) and (min-width: 769px) {
  .tech {
    margin: 30px 10px 30px 210px;
    padding: 35px 25px;
  }

  .techstack {
    grid-template-columns: repeat(3, 1fr);
    gap: 25px;
  }

  .tech h2 {
    font-size: 2rem;
  }

  .section-heading {
    font-size: 1.6rem;
  }
}

/* Tablets and smaller (below 768px) */
@media (max-width: 768px) and (min-width: 487px) {
  .tech {
    margin: 25px 10px 25px 210px;
    padding: 30px 20px;
  }

  .techstack {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    padding: 20px;
  }

  .tech h2 {
    font-size: 1.8rem;
  }

  .section-heading {
    font-size: 1.5rem;
  }

  .tech p {
    font-size: 0.95rem;
  }
}

/* Fix for devices between 481px and 486px to prevent layout break */
@media (max-width: 486px) and (min-width: 481px) {
  .tech {
    margin: 25px 10px 25px 60px; /* reduce left margin slightly */
    padding: 25px 15px;
  }

  .techstack {
    grid-template-columns: 1fr 1fr;
    gap: 18px;
    padding: 15px;
  }

  .tech h2 {
    font-size: 1.7rem;
  }

  .section-heading {
    font-size: 1.4rem;
  }

  .tech p {
    font-size: 0.9rem;
  }
}

/* Small Screens (Mobiles) */
@media (max-width: 480px) {
  .tech {
    margin: 20px 10px; /* no left margin */
    padding: 20px 15px;
  }

  .techstack {
    grid-template-columns: 1fr;
    padding: 15px 10px;
    gap: 15px;
  }

  .techstack .card {
    font-size: 0.95rem;
    padding: 14px;
  }

  .techstack .tech-icon {
    font-size: 2rem;
  }

  .tech h2 {
    font-size: 1.6rem;
  }

  .section-heading {
    font-size: 1.3rem;
  }

  .tech p {
    font-size: 0.9rem;
  }
}
